.App {
  text-align: center;
  justify-content: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.card {
  max-width: 1vw;
  min-width: 400px;
  max-height: 800px;
  justify-self: center;
  /* Template code for cross-platform centre positioning */
  margin: auto;
  position: absolute;
  top: 0; left: 0; bottom: 0; right: 0;
}

Button {
  margin:15px;
}
